.post-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 50px 0 50px 0;
}
.post-container img{
  cursor:pointer;
}
.post-container img,
.post-date-clock img {
  width: 100%;
}

.post-date {
  width: 140px;
  display: flex;
  flex-direction: column;
  margin-top: 20px;
}

.post-date-clock {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  line-height: 0;
}

.post-date-clock img {
  width: 18px;
  height: 18px;
  opacity: 0.6;
}
.post-date p {
  margin-top: 2px;
  color: #003a45;
  opacity: 0.9;
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 300;
}
.post-date-hr {
  width: 60%;
  margin-top: 10px;
  border-top: 0.5px solid #885b04;
}

.post-title {
  display: flex;
  align-items: flex-end;
  width: 320px;
  margin: 30px 0 30px 0;
}

.post-title img,
.post-title h1,
.post-description {
  opacity: 0.9;
}

.post-title img {
  width: 30px;
  height: 30px;
  margin-right: 20px;
}
.post-title h1 {
  font-size: 1.3rem;
  font-family: "Montserrat", sans-serif;
  font-weight: 800;
  color: #003a45;
}

.post-description {
  width: 100%;
  height:100px;
  padding-bottom: 30px;
  line-height: 1.5;
  overflow: hidden;
  color: #003a45;
}

.post-end-hr {
  width: 100%;
  margin-top: 30px;
  border-top: 1.5px solid #047388;
  height: 1px;
  opacity: 0.2;
}

.post-footer{
    display:flex;
    justify-content: space-between;
    margin-top: 20px;
   
}

.continue-reading{
    padding:5px 15px 5px 15px;
    background-color: #003A45;
    font-family: "Montserrat", sans-serif;
    font-variant:all-small-caps;
    color:white;
    opacity: 0.5;
}

.post-footer-socials{
    display:flex;
}

.post-footer-socials img{
    width:25px;
    height:25px;
    margin-right:10px;
    opacity: 0.5;
}
